p {

  color: #282c34;
}

*{

  padding: 0;
  margin: 0;
  text-decoration: none;
  font-family: "Roboto";
}
.style{

padding: 0 40px;

}

body {

  padding: 0px ;
  padding-bottom: none;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

